/* ubuntu-300 - latin_cyrillic */
@font-face {
  font-family: 'Ubuntu Light';
  font-style: normal;
  font-weight: 300;
  src: url('../fonts/ubuntu/ubuntu-v11-latin_cyrillic-300.eot'); /* IE9 Compat Modes */
  src: local('Ubuntu Light'), local('Ubuntu-Light'),
  url('../fonts/ubuntu/ubuntu-v11-latin_cyrillic-300.eot?#iefix') format('embedded-opentype'), /* IE6-IE8 */
  url('../fonts/ubuntu/ubuntu-v11-latin_cyrillic-300.woff2') format('woff2'), /* Super Modern Browsers */
  url('../fonts/ubuntu/ubuntu-v11-latin_cyrillic-300.woff') format('woff'), /* Modern Browsers */
  url('../fonts/ubuntu/ubuntu-v11-latin_cyrillic-300.ttf') format('truetype'), /* Safari, Android, iOS */
  url('../fonts/ubuntu/ubuntu-v11-latin_cyrillic-300.svg#Ubuntu') format('svg'); /* Legacy iOS */
}
